import {debounce} from 'lodash';

import React, {Component} from 'react';

class PeekAttributes extends Component {
    state = {
        attributes: {}
    }

    componentDidMount() {
        const update  = (e) => {
            const el = document.elementFromPoint(e.pageX, e.pageY);
            const attr = el.getAttribute('data-block');

            if(attr) {
                console.log(wp.data.select( 'core/block-editor' ).getBlocksByClientId(attr));
            }

          
        }

        const debouncedMM = debounce(update, 100);

        document.addEventListener('mousemove', debouncedMM);
    }
    render() {
        return (
            <div>{JSON.stringify(this.state.attributes)}</div>
        )
    }
}

export default PeekAttributes;